import { Fragment } from 'react'
import classnames from 'classnames'
import Select from 'react-select'
import Flatpickr from 'react-flatpickr'
import { selectThemeColors } from '@utils'
import { useForm, Controller } from 'react-hook-form'
import { ArrowRight } from 'react-feather'
import { useDispatch } from 'react-redux'
// import { yupResolver } from '@hookform/resolvers/yup'
import { Form, Label, Input, FormGroup, Row, Col, Button} from 'reactstrap'
import { addQuiz, getLang } from '../../views/Quiz/store/action'

import '@styles/react/libs/flatpickr/flatpickr.scss'
import '@styles/react/libs/react-select/_react-select.scss'
// import { QuizDetailsSchema } from './validations'

const colourOptions = [
  { value: 'Cricket', label: 'Cricket' },
  { value: 'Football', label: 'Football' },
  { value: 'Basketball', label: 'Basketball' }
]
const langOptions = [
  { value: '1', label: 'English' },
  { value: '2', label: 'Arabic' }
]
const QuizDetails = ({ stepper }) => {
  const dispatch = useDispatch()
  const { register, errors, handleSubmit, control, trigger } = useForm({
    // resolver: yupResolver(QuizDetailsSchema)
  })
  // const en = "1"
  const onSubmit = (val) => {
    console.log(val)
    const param = {
      // ...val,
      quiz_coins:val.quiz_coins,
      quiz_expiry: val.quiz_expiry[0],
      quiz_category: val.quiz_category.value,
      translation_quiz: [
        {
          master_language_id: val.quiz_lang.value,
          translation_quiz_name: val.quiz_name
        }
      ]}
    dispatch(addQuiz(param))
    dispatch(getLang(val.quiz_lang.value))
    trigger()
    console.log(param)
    stepper.next()
  }

  return (
    <Fragment>
      <div className="content-header">
        <h5 className="mb-0">Quiz Details</h5>
      </div>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Row>
          <FormGroup tag={Col} md="6">
            <Label className="form-label" for="quiz_name">
              quiz_name
            </Label>
            <Input
              name="quiz_name"
              id="quiz_name"
              placeholder="Quiz name"
              innerRef={register({ required: true })}
              className={classnames({
                'is-invalid': errors['quiz_name']
              })}
            />
          </FormGroup>
             <FormGroup tag={Col} md="3">
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
          <FormGroup tag={Col} md="3">
            <Label className="form-label" for="quiz_category">
              Quiz Category
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_category"
              control={control}
              name="quiz_category"
              rules={{ required: true }}
              options={colourOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_category']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
        </Row>
        <Row>
          <div className="form-group form-password-toggle col-md-6">
            <Label className="form-label" for="quiz_expiry">
              Expiry
            </Label>
            <Controller
              data-enable-time
              placeholder='Select...'
              as={Flatpickr}
              control={control}
              id="quiz_expiry"
              name="quiz_expiry"
              rules={{ required: true }}
              className={classnames('form-control flatpickr-input', {
                'is-invalid': errors['quiz_expiry']
              })}
            />
          </div>
          <div className="form-group form-password-toggle col-md-6">
            <Label className="form-label" for="quiz_coins">
              Quiz coins
            </Label>
            <Input
              type="number"
              placeholder='Select...'
              name="quiz_coins"
              id="quiz_coins"
              innerRef={register({ required: true })}
              className={classnames({
                'is-invalid': errors['quiz_coins']
              })}
            />
          </div>
        </Row>
        <div className="d-flex justify-content-end">
          {/* <Button.Ripple
            color="secondary"
            className="btn-prev"
            outline
            disabled
          >
            <ArrowLeft
              size={14}
              className="align-middle mr-sm-25 mr-0"
            ></ArrowLeft>
            <span className="align-middle d-sm-inline-block d-none">
              Previous
            </span>
          </Button.Ripple> */}
          <Button.Ripple type="submit" color="primary" className="btn-next">
            <span className="align-middle d-sm-inline-block d-none">Next</span>
            <ArrowRight
              size={14}
              className="align-middle ml-sm-25 ml-0"
            ></ArrowRight>
          </Button.Ripple>
        </div>
      </Form>
    </Fragment>
  )
}

export default QuizDetails
